:root {
  --tomato: #ff6257;
  --dark-slate-gray: #242742;
  --charcoal-grey: #36384e;
  --grey: #9294a0;
  --white: #fff;
  --body-font-size: 16px;
}

*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans;
}

html, body {
  background-color: var(--charcoal-grey);
  height: 100%;
}

body {
  justify-content: center;
  align-items: center;
  display: flex;
}

img {
  width: 100%;
}

h1 {
  color: var(--dark-slate-gray);
  margin-block-end: 20px;
  font-size: 48px;
  line-height: 110%;
}

p {
  line-height: 150%;
}

a, a:visited, a:hover, a:active {
  color: var(--dark-slate-gray);
  font-weight: 700;
  text-decoration: none;
}

.card {
  background-color: var(--white);
  border-radius: 25px;
  padding: 20px;
  display: flex;
}

.card-left {
  margin: 29px;
  margin-top: 49px;
  margin-right: 49px;
}

.card-left > p {
  margin-block-end: 20px;
}

.card-left > ul {
  width: 100%;
  margin-block-end: 20px;
}

.card-left li {
  background: url("../icon-list.633b42af.svg") 0 no-repeat;
  padding-inline-start: 35px;
  line-height: 200%;
  list-style-type: none;
}

.card-left label {
  color: var(--dark-slate-gray);
  font-size: 12px;
  font-weight: 700;
}

.card-left input {
  border: 2px solid var(--grey);
  border-radius: 7px;
  width: 100%;
  margin-block: 5px 20px;
  padding: 15px;
}

.card-left button {
  background-color: var(--dark-slate-gray);
  color: var(--white);
  cursor: pointer;
  border-radius: 10px;
  width: 100%;
  padding-block: 20px;
  font-weight: 700;
}

.card-thanks-for-subscribing {
  color: var(--dark-slate-gray);
  width: 460px;
  padding: 50px;
}

.card-thanks-for-subscribing img {
  width: 50px;
  margin-block-end: 20px;
}

.card-thanks-for-subscribing p {
  margin-block-end: 30px;
}

.card-thanks-for-subscribing > button {
  background-color: var(--dark-slate-gray);
  color: var(--white);
  border-radius: 10px;
  width: 100%;
  padding-block: 20px;
}

@media (width <= 1200px) {
  .card {
    border-radius: 15px;
    flex-direction: column-reverse;
    width: 400px;
    padding: 0;
    overflow: hidden;
  }

  .card-thanks-for-subscribing {
    padding: 50px;
  }
}

